@import "./styles/reset";

@font-face {
	font-family: Mont;
	font-display: swap;
	src: url("./fonts/Mont-Regular.woff2") format("woff2"), url("./fonts/Mont-Regular.woff") format("woff");
	font-weight: 400;
	font-style: normal;
}

@font-face {
	font-family: Mont;
	font-display: swap;
	src: url("./fonts/Mont-SemiBold.woff2") format("woff2"), url("./fonts/Mont-SemiBold.woff") format("woff");
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: Mont;
	font-display: swap;
	src: url("./fonts/Mont-Bold.woff2") format("woff2"), url("./fonts/Mont-Bold.woff") format("woff");
	font-weight: 700;
	font-style: normal;
}

@import "./styles/utils";
